import React, { Component } from 'react';
import UserAPI from '../../api/user';
import {
    Card, CardImg, CardText, CardBody,
    CardTitle, Button
} from 'reactstrap';
import queryString from 'query-string';

class Profile extends Component {

    constructor(props) {
        super(props);
        this.state = {
            uid: "",
            firstName: "",
            lastName: "",
            email: "",
            phoneNumber: ""
        }

        this.edit = this.edit.bind(this);
    }

    edit(e) {
        e.preventDefault();
        console.log('sign up user');
        this.props.history.push('/profile/update?uid='+this.state.uid);
    }
    
    componentDidMount() {
        console.log('componentDidMount...');
        let url = this.props.location.search;
        let params = queryString.parse(url);
        console.log(params);

        UserAPI.getCurrentUser(params.uid)
            .then(response => {
                console.log("response");
                console.log(response.data);
                this.setState(response.data);
                this.setState({uid: params.uid});

                let createdAt = new Date(this.state.createdAt);

                console.log("createdAt",createdAt);

                
            }).catch(error => {
                console.log("error");
                console.log(error.response.data);
            });
    }

    render() {
        return (
            <div>
                <div className="row">
                    <div className="col-sm-1 col-md-3">

                    </div>
                    <div className="col-sm-10 col-md-6">
                        Profile
                        <br />
                    </div>
                    <div className="col-sm-1 col-md-3">

                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-1 col-md-3">

                    </div>
                    <div className="col-sm-10 col-md-6">
                        <Card>
                            <CardImg top width="50%" src={this.state.profileImgUrl} alt="profile" />
                            <CardBody>
                                <CardTitle>{this.state.firstName} {this.state.lastName}</CardTitle>
                                <CardText>ID: #{this.state.id}</CardText>
                                <CardText>Email: {this.state.email}</CardText>
                                <CardText>DOB: {this.state.dob}</CardText>
                                <CardText>Email: {this.state.email}</CardText>
                                <CardText>Phone: {this.state.phoneNumber}</CardText>
                                <CardText>Updated At: {this.state.updatedAt}</CardText>
                                <CardText>Created At: {this.state.createdAt}</CardText>
                                
                                <Button onClick={this.edit} >edit</Button>

                            </CardBody>
                        </Card>
                    </div>
                    <div className="col-sm-1 col-md-3">

                    </div>
                </div>

            </div>
        )
    }
}

export default Profile;